function TimesheetStrategy(rowKeyClass, columnKeyClass, options) {
    this.rowKeyClass = rowKeyClass;
    this.columnKeyClass = columnKeyClass;
    this.options = TimesheetStrategy.getStrategyOptions(options);
    this.configOptions = options.configOptions;
}

TimesheetStrategy.getStrategyOptions = function(options) {
    var monthView = options.monthView;
    var calculatedStartDate =
        options.endDate
            ? TimesheetUtils.addDays(options.endDate, -6)
            : TimesheetUtils.getLastMondayDate(options.reportingDay);
    var startDate =
        options.startDate
            ? moment(options.startDate).toDate()
            : monthView
                ? TimesheetUtils.addDays(calculatedStartDate, -3*7)
                : calculatedStartDate;
    var endDate =
        options.endDate
            ? moment(options.endDate).toDate()
            : monthView
                ? TimesheetUtils.addDays(startDate, 4 * 7 - 1)
                : TimesheetUtils.addDays(startDate, 6);
    var result = {
        startDate: startDate,
        endDate: endDate
    };
    if (options.worklogAuthors) {
        result.worklogAuthors = options.worklogAuthors;
    } else if (options.user) {
        result.worklogAuthors = [options.user];
    }
    result.includeEmpty = options.includeEmpty;
    result.reportingDay = options.reportingDay;
    result.showWeekends = options.showWeekends;
    result.groupByField = options.groupByField;
    result.groupByFieldObject = options.groupByFieldObject;
    result.categorizeByField = options.categorizeByField;
    result.categorizeByFieldObject = options.categorizeByFieldObject;
    return result;
};

TimesheetStrategy.prototype.getEntries = function(issue) {
    var entries = [];
    var issueWorklog = issue.worklog || issue.fields.worklog;
    for (var i = 0; i < issueWorklog.worklogs.length; i++) {
        var worklog = issueWorklog.worklogs[i];
        var workDate = worklog.workDate = TimesheetUtils.convertDate(worklog.started,
            !this.configOptions.preserveStartedTime && worklog.author ? worklog.author.timeZone : null);
        var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1); // endDate should be in range
        if (workDate >= this.options.startDate && workDate < endDateIncluded) { // filtering by start and end dates
            if (!this.options.worklogAuthors ||  // filtering by author
                (worklog.author && // add FIXME: worklogAuthorNames for ACJIRA-300
                    this.options.worklogAuthors.indexOf(worklog.author.key ? worklog.author.key : worklog.author.name.toLowerCase()) >= 0)) {
                var rowKey = new this.rowKeyClass(issue, worklog, this.options);
                var columnKey = new this.columnKeyClass(workDate, this.options);
                var entry = new WorkedTimePivotEntry(rowKey, columnKey, worklog);
                entries.push(entry);
            }
        }
    }
    if (this.options.includeEmpty && entries.length == 0 && ['workeduser', 'workeddate', 'workcreated'].indexOf(this.options.groupByField) < 0) {
        var rowKey = new this.rowKeyClass(issue, null, this.options);
        var entry = new EmptyPivotEntry(rowKey);
        entries.push(entry);
    }
    return entries;
};

TimesheetStrategy.prototype.getYearColumns = function() {
    return this.getColumns(TimesheetUtils.getMonthForYear);
};

TimesheetStrategy.prototype.getMonthColumns = function() {
    return this.getColumns(TimesheetUtils.getWeeksForMonth);
};

TimesheetStrategy.prototype.getWeekColumns = function() {
    return this.getColumns(TimesheetUtils.getDatesForWeek);
};

TimesheetStrategy.prototype.getDayColumns = function() {
    return this.getColumns(TimesheetUtils.getHoursForDay);
};

TimesheetStrategy.prototype.getColumns = function(sourceFn) {

    return sourceFn(this.options, this.configOptions).map(function(c) {
        var column = new this.columnKeyClass(moment(c.date).toDate(), this.options);
        column.isToday = c.isToday;
        column.isWeekend = c.isWeekend;
        return column;
    }, this).reduce(function(result, column) {
        result[column.keyValue] = new PivotColumn(column);
        return result;
    }, {});
};
