//This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with
//each doing the same thing just structuring the functions/data differently.
app.service('pivottableService', ['$q', '$timeout', '$interval', '$http', 'applicationLoggingService', 'configurationService',
        function ($q, $timeout, $interval, $http, logger, configurationService) {
    var AP = window.RA || window.AP;

    var DASHBOARD_PROPERTY_GADGET_CONFIG = 'gadgetConfig';

    const MAX_BULK_LENGTH = 5;

    this.permissions = {};
    // filterWorklogs()
    this.userGroups = {};
    this.worklogAuthors = [];
    // isEpicField()
    this.epicFields = {};
    this.workLabels = {};
    this.issueWorklogs = [];
    this.issueChangelogs = [];
    this.findIssueByKeyPromises = [];
    this.getPermissionsPromises = [];
    logger._ = Date.now(); // session tracking id

    this.getPivotTable = function(loggedInUser, options) {

        // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
        if (options.groups && options.groups.length > 0 && !options.configOptions.storeWorklog) {
            options.worklogAuthors = this.worklogAuthors;
        }

        var pivotTable = PivotTableFactory.createPivotTable(options, logger);
        var deferred = $q.defer();
        if (this.checkAndUpdateOptions(options)) {
            pivotTable.queue = {};
            pivotTable.queueToAdd = [];
            pivotTable.matches = {};
            var service = this;
            if (this.groups && this.groups.length > 0) {
                this.userGroups[loggedInUser.key] = $q.resolve(loggedInUser);
                if (!options.configOptions.storeWorklog) {
                    this.initWorklogAuthors(loggedInUser);
                }
            }
            if (!options.configOptions.storeWorklog || ['TimeTracking', 'IssuePassedTimeByStatus'].indexOf(options.pivotTableType) != -1) {
                var performance = TimesheetUtils.getPerformance();
                var t0 = performance.now();
                var m = 0, n = 0, notify;
                var $notify = $interval(function() {
                    // 6250 ms till 10% for 10K issues
                    if (!notify) {
                        deferred.notify(++n/250);
                    } else {
                        $interval.cancel($notify);
                    }
                }, 250);
                this.onAllIssues(this.getQuery(pivotTable, options), function(data) {
                    var t1 = performance.now();
                    if (!notify) {
                        n = data.total || 1;
                        notify = function() {
                            deferred.notify(++m/n);
                        };
                    }
                    var result = service.processIssues(pivotTable, options, data, notify);
                    var t2 = performance.now();
                    //logger.debug('processIssues performance', {_: logger._, time: t2 - t1, t1: t1 - t0});
                    return result;
                }).then(function(result) {
                    if (service.cancel(deferred)) {
                        return;
                    }
                    var promises = result.reduce(function(a, b) { // flatten
                      return a.concat(b);
                    });
                    var t2 = performance.now();
                    // objects may have 2^32 - 1 properties: http://stackoverflow.com/questions/9282869/javascript-limits-to-number-of-object-properties
                    $q.all(promises).then(function() {
                        if (service.cancel(deferred)) {
                            return;
                        }
                        angular.forEach(pivotTable.queueToAdd, function(issue) {
                            service.addIssue(pivotTable, issue);
                        });
                        deferred.resolve(pivotTable);
                        var t3 = performance.now();
                        //service.allIssues.length > 100 && logger.debug('getPivotTable performance', {/*_: logger._  , sumSubTasks: options.sumSubTasks, groups: options.groups, allIssues: service.allIssues.length, promises: promises.length,*/ time: t3 - t0, t2: t2 - t0, t3: t3 - t2});
                    }, deferred.reject);
                }, function(error) {
                    $interval.cancel($notify);
                    deferred.reject(error);
                });
            } else {
                service.getAndProcessIssues(pivotTable, options).then(function() {
                    angular.forEach(pivotTable.queueToAdd, function(issue) {
                        service.addIssue(pivotTable, issue);
                    });
                    deferred.resolve(pivotTable);
                }, deferred.reject, deferred.notify);
            }
        } else {
            for (var i in this.allIssues) {
                var pivotEntries = pivotTable.add(this.allIssues[i]);
            }
            $timeout(function() { // for PieChart
                deferred.resolve(pivotTable);
            });
        }
        return deferred.promise;
    };

    this.cancel = function(deferred) {
        if (this._cancel && deferred) {
            deferred.reject('cancelled');
            this._cancel = false;
            return true;
        } else if (!deferred) {
            this._cancel = true;
        }
        return false;
    };

   this.getAndProcessIssues = function(pivotTable, options) {
        var promises = [];
        var service = this;
        var deferred = $q.defer();
        var queryBuilder = new TimesheetUtils.queryBuilder();
        if (options.startDate != null && options.startDate != "") {
            queryBuilder.addQuery('start', service.addDays(options.startDate, -1)); // -1 Day to startDate to cover all timezones
        }
        if (options.endDate != null && options.endDate != "") {
            queryBuilder.addQuery('end', service.addDays(options.endDate, 2)); // +1 Day to endDate to cover all timezones
        }
        if (options.user) {
            queryBuilder.addQuery('author', options.user); // filter by user
        }
        if (options.groups) {
          queryBuilder.addQuery('groups', options.groups); // filter by groups
        }
        if (options.jql != null) {
            queryBuilder.addQuery('jql', options.jql); // empty JQL for all issues
        } else if (options.filterOrProjectId) {
            queryBuilder.addQuery('filterOrProjectId', options.filterOrProjectId);
        }
        if (options.projectKey) {
            queryBuilder.addQuery('projectKey', options.projectKey);
        }
        if (options.sumSubTasks) {
            queryBuilder.addQuery('sumSubTasks', options.sumSubTasks);
        }
        var moreFields = this.getMoreFields(options);
        if (moreFields.length > 0) {
            queryBuilder.addQuery('moreFields', moreFields);
        }

        var fields = 'project,issuetype,resolution,summary,priority,status,parent,issuelinks';
        this.initEpicLinkField();
        if (this.epicLinkField) {
            fields += ',' + this.epicLinkField.id;
        }
        if (moreFields.length > 0) {
            fields += ',' + moreFields.join(',');
        }
        if (this.isProcessChildren(options)) {
            fields += ',subtasks'; // for processChildren
        }
        options._fields = fields.replace(',worklog', ''); // for findIssueByKey

        queryBuilder.addQuery('_', Date.now());
        var query = queryBuilder.build();

        var performance = TimesheetUtils.getPerformance();
        var t0 = performance.now();
        var m, n = 0, $notify = $interval(function() {
            // 31 seconds till 50%
            deferred.notify(++n/250);
        }, 250);
        $http.get("/api/worklog?" + query).success(function(worklog) {
            $interval.cancel($notify);
            if (service.cancel(deferred)) {
                return;
            }
            var t1 = performance.now();
            m = n * worklog.length / 250; // continue
            n = (worklog.length - m) / worklog.length; // increment, may be NaN = 0/0
            var promises = service.processIssues(pivotTable, options, {issues: worklog}, function() {
                m += n;
                deferred.notify(m / worklog.length);
            });
            $q.all(promises).then(deferred.resolve);
            var t2 = performance.now();
            logger.debug('getAndProcessIssues', {_: logger._, t2: t2 - t1, t1: t1 - t0, t: t2 - t0});
        }).error(function() {
            console.log(arguments);
        });
        return deferred.promise;
    };

    this.onAllIssues = function(query, callback) {
        var startAt = (query ? '&' : '') + 'startAt=';
        var firstDeferred = this.getIssues(query + startAt + '0');
        var deferred = $q.defer();
        var promise = firstDeferred.then(callback);
        var promises = [promise];
        var service = this;
        firstDeferred.then(function(data) {
            var n = Math.ceil(data.total / data.maxResults) || 1;
            for (var i = 1; i < n; i++) {
                (function(def, i) { // closure in loop
                    var start = Math.floor(i / 5) * 5 + 1; // batch of 5 concurrent requests
                    var previous = i > 5 ? promises.slice(start, start + 5) : [];
                    $q.all(previous).then(function() {
                        if (service.cancel(deferred)) {
                            return;
                        }
                        service.getIssues(query + startAt + data.maxResults * i)
                            .then(callback)
                            .then(def.resolve, def.reject);
                    });
                    promises.push(def.promise);
                })($q.defer(), i);
            }
            $q.all(promises).then(deferred.resolve, deferred.reject);
        }, deferred.reject);
        return deferred.promise;
    };

    this.getFields = function() {
        var pivottableService = this;
        var deferred = $q.defer();

        if (pivottableService.allFields == null) {
            var url = '/rest/api/2/field';
            AP.request({
                url: url,
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    pivottableService.allFields = data;
                    deferred.resolve(data);
                },
                error: function(jqXHR) {
                    var args = Array.prototype.slice.call(arguments);
                    args.push(url, deferred);
                    pivottableService.onError.apply(pivottableService, args);
                }
            });
        } else {
            deferred.resolve(pivottableService.allFields);
        }

        return deferred.promise;
    };

    this.getFilters = function() {
        var pivottableService = this;
        var deferred = $q.defer();

        if (pivottableService.allFilters == null) {
            var url = '/rest/api/2/filter/favourite';
            AP.request({
                url: url,
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    pivottableService.allFilters = data;
                    deferred.resolve(data);
                },
                error: function(jqXHR) {
                    var args = Array.prototype.slice.call(arguments);
                    args.push(url, deferred);
                    pivottableService.onError.apply(pivottableService, args);
                }
            });
        } else {
            deferred.resolve(pivottableService.allFilters);
        }
        return deferred.promise;
    };

    this.getJiraConfiguration = function() {
        var deferred = $q.defer();
        AP.request({
            url: '/rest/api/2/configuration',
            dataType: 'json',
            success: function(data) {
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function() {
                deferred.resolve(); // ignore
            }
        });
        return deferred.promise;
    };

    this.getUserInfo = function(username) {
        var deferred = $q.defer();
        if (!username) {
            deferred.resolve(null);
        } else {
            var service = this;
            var url = '/rest/api/2/user?expand=groups&key=' + encodeURIComponent(username);
            AP.request({
                url: url,
                dataType: 'json',
                success: function (data) {
                    deferred.resolve(TimesheetUtils.getJson(data));
                },
                error: function(jqXHR) {
                    if (jqXHR.status == 404) {
                        deferred.resolve();
                    } else {
                        var args = Array.prototype.slice.call(arguments);
                        args.push(url, deferred);
                        service.onError.apply(service, args);
                    }
                }
            });
        }
        return deferred.promise;
    };

    this.getIssues = function(query) {
        var pivottableService = this;
        var deferred = $q.defer();
        var url = '/rest/api/2/search?' + query;
        AP.request({
            url: url,
            dataType: 'json', // does not work!
            success: function(data) {
                data = TimesheetUtils.getJson(data);
                deferred.resolve(data);
            },
            error: function() {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                pivottableService.onError.apply(pivottableService, args);
            }
        });
        return deferred.promise;
    };

    this.getQuery = function(pivotTable, options) {
        var pivottableService = this;
        // https://ecosystem.atlassian.net/browse/ADDON-230
        var maxResults = options.isGadget ? 500 : 1000; // https://confluence.atlassian.com/display/JIRAKB/Changing+maxResults+Parameter+for+JIRA+REST+API

        var quote = function(value) {
            return '"' + value + '"';
        };

        var prepareJqlBuilder = function(options) {
            var jqlQueryBuilder;
            if (!options.includeEmpty && options.sumSubTasks && options.pivotTableType != 'TimeTracking') {
                pivottableService.initMatches(options);
                jqlQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
            } else {
                jqlQueryBuilder = TimesheetUtils.getSearchQueryBuilder(options);
            }
            var isIssuePassedTimeByStatus = options.pivotTableType == 'IssuePassedTimeByStatus';
            var isTimeTracking = options.pivotTableType == 'TimeTracking';
            if (!options.includeEmpty && options.startDate != null && options.startDate != "") {
                var dateClause = isIssuePassedTimeByStatus ? 'status changed' : isTimeTracking ? 'updated' : 'worklogDate';
                var dateCondition = isIssuePassedTimeByStatus ? ' after ' : '>=';
                var qb = isIssuePassedTimeByStatus || isTimeTracking ? new TimesheetUtils.queryBuilder(" or ") : jqlQueryBuilder;
                qb.addQuery(dateClause, quote(pivottableService.addDays(options.startDate, -1)), dateCondition); // -1 Day to startDate to cover all timezones for worklogs, or include report day for status change
                if (isIssuePassedTimeByStatus || isTimeTracking) {
                    qb.addQuery('created', quote(pivottableService.addDays(options.startDate, -1)), '>');
                    jqlQueryBuilder.addQueryBuilder(qb);
                }
            }
            if (!options.includeEmpty && options.endDate != null && options.endDate != "") {
                var dateClause = isIssuePassedTimeByStatus ? 'status changed' : isTimeTracking ? 'updated' : 'worklogDate';
                var dateCondition = isIssuePassedTimeByStatus ? ' before ' : '<=';
                var qb = isIssuePassedTimeByStatus || isTimeTracking ? new TimesheetUtils.queryBuilder(" or ") : jqlQueryBuilder;
                qb.addQuery(dateClause, quote(pivottableService.addDays(options.endDate, 1)), dateCondition); // +1 Day to endDate to cover all timezones for worklogs, or include report day for status change
                if (isIssuePassedTimeByStatus || isTimeTracking) {
                    qb.addQuery('created', quote(pivottableService.addDays(options.endDate, 1)), '<');
                    jqlQueryBuilder.addQueryBuilder(qb);
                }
            }
            var jqlEscape = function(value) {
                return value
                    .replace('"', '\\u0022')
                    .replace('#', '\\u0023')
                    .replace('$', '\\u0024')
                    .replace('%', '\\u0025')
                    .replace('*', '\\u002a')
                    .replace('@', '\\u0040')
                    .replace('^', '\\u005e');
                // <>& unsupported by user management
            };
            var authorClause = isIssuePassedTimeByStatus ? 'status changed' : isTimeTracking ? 'assignee' : 'worklogAuthor';
            var authorCondition = isIssuePassedTimeByStatus ? ' by ' : '=';
            if (!options.includeEmpty && options.username) { // ACJIRA-300
                var username = jqlEscape(options.username);
                var qb = isIssuePassedTimeByStatus || isTimeTracking ? new TimesheetUtils.queryBuilder(" or ") : jqlQueryBuilder;
                qb.addQuery(authorClause, quote(username), authorCondition);
                if (isIssuePassedTimeByStatus || isTimeTracking) {
                    qb.addQuery('reporter', quote(username));
                    jqlQueryBuilder.addQueryBuilder(qb);
                }
            }
            if (!options.includeEmpty && options.groups && options.groups.length > 0) {
                var memberOfQB = new TimesheetUtils.queryBuilder(" or ");
                for (var i = 0; i < options.groups.length; i++) {
                    var authorCondition = isIssuePassedTimeByStatus ? ' by ' : ' in ';
                    memberOfQB.addQueryWithFunc(authorClause, quote(jqlEscape(options.groups[i])), authorCondition, "membersOf");
                    if (isIssuePassedTimeByStatus || isTimeTracking) {
                        memberOfQB.addQueryWithFunc('reporter', quote(jqlEscape(options.groups[i])), ' in ', "membersOf");
                    }
                }
                jqlQueryBuilder.addQueryBuilder(memberOfQB);
            }
            return jqlQueryBuilder;
        };

        var fields = 'project,issuetype,resolution,summary,priority,status,parent,issuelinks';
        if (['TimeTracking', 'IssuePassedTimeByStatus'].indexOf(options.pivotTableType) < 0) {
            fields += ',worklog';
            if (options.includeEmpty) {
                maxResults /= 2;
            }
        }
        if ('TimeTracking' == options.pivotTableType) {
            fields += ',' + this.timeFields.join(',');
        }
        this.initEpicLinkField();
        if (this.epicLinkField) {
            fields += ',' + this.epicLinkField.id;
        }
        if (options.pivotTableType == 'IssuePassedTimeByStatus') {
            fields += ',created';
        }
        var moreFields = this.getMoreFields(options);
        if (moreFields.length > 0) {
            fields += ',' + moreFields.join(',');
        }
        if (this.isProcessChildren(options)) {
            fields += ',subtasks'; // for processChildren
        }
        options._fields = fields.replace(',worklog', '');

        var queryBuilder = new TimesheetUtils.queryBuilder()
            // fields=worklog or expand=worklog
            .addQuery('fields', fields)
            // maxResults <= jira.search.views.default.max = 1000
            .addQuery('maxResults', maxResults);
        if (['IssueWorkedTimeByStatus', 'IssuePassedTimeByStatus'].indexOf(options.pivotTableType) >= 0) {
            queryBuilder.addQuery('expand', 'changelog');
        }

        var jqlQueryBuilder = prepareJqlBuilder(options);

        if (!jqlQueryBuilder.isEmpty()) {
            var jqlQuery = jqlQueryBuilder.build();
            queryBuilder.addQuery('jql', encodeURIComponent(jqlQuery));
            var issuePickerJqlQueryBuilder = TimesheetUtils.getSearchQueryBuilder(options);
            options.jqlQuery = issuePickerJqlQueryBuilder.build();
        }
        var query = queryBuilder.build();
        return query;
    };

    this.timeFields = ['timeoriginalestimate', 'timeestimate', 'timespent'];

    this.isProcessChildren = function(options) {
        var moreFields = this.getMoreFields(options);
        return options.sumSubTasks &&
            (options.pivotTableType == 'TimeTracking' || TimesheetUtils.findOne(moreFields, this.timeFields))
            && (!options.groupByField || (!this.isEpicField(options.groupByField) && this.epicLinkField && this.epicLinkField.id != options.groupByField));
    };

    this.processIssues = function(pivotTable, options, data, notify) {
        var promises = [];
        for (var i in data.issues) {
            var issue = data.issues[i];
            if (['TimeTracking', 'IssuePassedTimeByStatus'].indexOf(options.pivotTableType) >= 0) {
                 issue.fields.worklog = {total: 0, maxResults: 0, worklogs: []};
            }
            var promise = this.processIssue(pivotTable, options, issue);
            promise.then(notify);
            promises.push(promise);
        }
        return promises;
    };

    this.processIssue = function(pivotTable, options, issue) {
        var deferred = $q.defer();
        var pivottableService = this;
        if (!options.sumSubTasks) {
            var promises = [];
            if (options.groupByField == 'parent') {
                var parent = this.getParentIssue(pivotTable, issue, options);
                if (parent.hasOwnProperty('promise')) {
                    var parentDeferred = $q.defer();
                    promises.push(parentDeferred.promise);
                    pivottableService.resolveParent(pivotTable, issue, parentDeferred, options, parent);
                } else {
                    issue.fields.parent = issue; // self
                }
            } else {
                this.getEpicFields(pivotTable, issue, options, promises);
            }
            var result = this.loadAllWorklogs(issue, null, options); // issue or deferred
            if (result.hasOwnProperty('promise')) {
                promises.push(result.promise); // wait for filterWorklogs to collect worklogAuthors
            }
            var changelogs = this.loadAllChangelogs(issue, options); // issue or deferred
            if (changelogs.hasOwnProperty('promise')) {
                promises.push(changelogs.promise);
            }
            $q.all(promises).then(function() {
                // with all worklogs and resolved parent
                pivottableService.addIssue(pivotTable, issue);
                deferred.resolve(issue);
            });
        } else {

            var checkInQueue = function (element) {
                if (!element.promise.$$state || element.promise.$$state.status == 0) { // pending
                    element.resolve(issue); // don't wait for findIssueByKey
                } else { // findIssueByKey is done without worklog
                    element.promise.then(function (issueFromQueue) {
                        issueFromQueue.fields.worklog = issue.worklog || issue.fields.worklog;
                    });
                }
            };

            var result = this.getParentIssue(pivotTable, issue, options);
            if (result.hasOwnProperty('promise')) {
                if (pivotTable.queue.hasOwnProperty(issue.key)) {
                    checkInQueue(pivotTable.queue[issue.key]);
                }
                this.loadAllWorklogs(issue, $q.defer(), options).promise.then(function(issue) {
                    pivottableService.addWorklogsToParent(pivotTable, issue, deferred, options, result);
                });
            } else {
                // highest parent - add to queue if matches filter
                var issueFromQueue;
                if (!pivotTable.queue.hasOwnProperty(issue.key)) {
                    pivotTable.queue[issue.key] = deferred;
                    issueFromQueue = issue;
                } else {
                    checkInQueue(pivotTable.queue[issue.key]);
                    issueFromQueue = pivotTable.queue[issue.key].promise;
                }
                $q.when(issueFromQueue).then(function(issue) {
                    if (issue) {
                        // the one that was used to resolve the promise
                        pivottableService.addOnceIfMatches(pivotTable, issue, options).then(function(/*matches*/) {
                            deferred.resolve(issue); // for onParentIssue
                        });
                    } else { // jiratimesheet issue#1000 parent issue not accessible
                        deferred.resovle();
                    }
                });
            }
        }
        return deferred.promise;
    };

    this.removeIssue = function (issue) {
        for (var i in this.allIssues) {
            if (this.allIssues[i].key === issue.key) {
                this.allIssues.splice(i,1);
            }
        }
    };

    this.addIssue = function(pivotTable, issue) {
        this.allIssues.push(issue);
        var pivotEntries = pivotTable.add(issue);
    };

    // TIME-4 -> TIME-3 -> TIME-2 -> TIME-1
    // TIME-3 - skip
    // TIME-2 - skip
    // TIME-1 - mark for add!
    // { TIME-4, TIME-3, TIME-2 promises, TIME-1 issue with all worklogs}
    // TIME-5 -> TIME-1

    // returns issue or parent deferred
    this.getParentIssue = function(pivotTable, issue, options) {
        var parentKey = this.getParentIssueKey(issue, options);
        if (parentKey) {
            return this.getParentIssueByKey(pivotTable, issue, parentKey, options);
        } else {
            return issue;
        }
    };

    this.getParentIssueByKey = function(pivotTable, issue, parentKey, options) {
        var pivottableService = this;
        if (pivotTable.queue.hasOwnProperty(parentKey)) { // known parent
            return pivotTable.queue[parentKey]; // parent is being queued
        } else { // new parent - queue
            var deferred = $q.defer();
            pivotTable.queue[parentKey] = deferred;
            pivottableService.findIssueByKey(parentKey, options, deferred);
            return deferred;
        }
    };

    this.getParentIssueKey = function(issue, options) {
        var configOptions = options.configOptions;
        if (issue.fields.parent) {
            return issue.fields.parent.key;
        } else if (configOptions && configOptions.parentIssueField && issue.fields[configOptions.parentIssueField]) {
            return issue.fields[configOptions.parentIssueField];
        } else if (configOptions && configOptions.compositionIssueLink) {
            return this.getCompositionLinkKey(issue, configOptions);
        } else {
            return null;
        }
    };

    this.getCompositionLinkKey = function(issue, configOptions) {
        var links = issue.fields.issuelinks;
        if (links != null && links.length > 0 /* paranoic && configOptions.compositionIssueLink */) {
            for (var i = 0; i < links.length; i++) {
                // if links[i].outwardIssue issue blocks links[i].outwardIssue
                // if links[i].inwardIssue present - issue is blocked by links[i].inwardIssue
                if (links[i].outwardIssue && links[i].type.name == configOptions.compositionIssueLink){
                    return links[i].outwardIssue.key;
                }
            }
        }
        return null;
    };

    this.resolveParent = function(pivotTable, issue, deferred, options, parent) {
        this.onParentIssue(pivotTable, issue, options, parent, [], function(data) {
            issue.fields.parent = data ? data : issue; // self
            deferred.resolve();
        });
    };

    this.initMatches = function(options) {
        if (!this.matches) {
            var jqlQueryBuilder = TimesheetUtils.getSearchQueryBuilder(options);
            var jqlQuery = jqlQueryBuilder.build();
            if (jqlQuery && (options.jql || options.filterIds.length > 0)) {
                var query = "fields=~&maxResults=1000&jql=" + encodeURIComponent(jqlQuery); // exclude all fields
                this.matchIssueIds = [];
                this.matches = this.onAllIssues(query, function(data) {
                    for (var i in data.issues) {
                        var issue = data.issues[i];
                        this.matchIssueIds.push(issue.id);
                    }
                }.bind(this));
            } else { // do not search for all issues
                var def = $q.defer();
                this.matches = def.promise;
                def.resolve();
                delete this.matchIssueIds;
            }
        }
    };

    this.processChildren = function(pivotTable, issue, options, previousIssues) {
        if (!this.isProcessChildren(options)) {
            return null;
        }

        if (previousIssues.indexOf(issue.key) < 0) {
            previousIssues.push(issue.key);
        } else {
            return null;
        }
        var subtasks = [];
        var service = this;
        var epicPromise =  null;
        if (issue.fields.issuetype.name == 'Epic' && this.epicLinkField) {
            var jqlQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
            jqlQueryBuilder.addQuery(this.epicLinkField.clauseNames[0], issue.key);
            var jqlQuery = jqlQueryBuilder.build();
            if (jqlQuery.length != 0) {
                var query = "fields=issuetype,resolution,issuelinks,timeoriginalestimate,timeestimate,timespent," + this.epicLinkField.id + ",subtasks&maxResults=1000&jql=" + encodeURIComponent(jqlQuery);
                var epicDeferred = $q.defer();
                epicPromise = epicDeferred.promise;
                var epicPromises = [];
                var promise = service.onAllIssues(query, function(data) {
                    for (var i in data.issues) {
                        var child = data.issues[i];
                        child.fields.worklog = {total: 0, maxResults: 0, worklogs: []}; // we need numeric fields only
                        var $epicIssues = /* nullable */ service.processChildren(pivotTable, child, options, previousIssues);
                        epicPromises.push($epicIssues);
                        $q.when($epicIssues).then((function(child) {
                            return function() {
                                service.addWorklogs(child, issue); // sum numeric fields
                            }
                        })(child)); // closure in loop
                    }
                });
                promise.then(function() { // wait for all children
                    $q.all(epicPromises).then(epicDeferred.resolve);
                });
            }
        }
        for (var i in issue.fields.subtasks) {
            var subtask = issue.fields.subtasks[i];
            subtasks.push(subtask.key);
        }
        // getCompositionLinkKeys inward!
        var links = issue.fields.issuelinks;
        var configOptions = options.configOptions;
        if (links != null && links.length > 0 && configOptions.compositionIssueLink) {
            for (var i = 0; i < links.length; i++) {
                // if links[i].outwardIssue issue blocks links[i].outwardIssue
                // if links[i].inwardIssue present - issue is blocked by links[i].inwardIssue
                if (links[i].inwardIssue && links[i].type.name == configOptions.compositionIssueLink){
                    var subtask = links[i].inwardIssue;
                    subtasks.push(subtask.key);
                }
            }
        }
        if (subtasks.length > 0) {
            var subtasksDeferred = $q.defer();
            if (subtasks.length > 0) {
                var subtasksJql = "key in (" + subtasks.join() + ")";
                var query = "fields=issuetype,issuelinks,timeoriginalestimate,timeestimate,timespent&maxResults=1000&jql=" + encodeURIComponent(subtasksJql);
                var subtasksPromises = [epicPromise];
                var promise = service.onAllIssues(query, function(data) {
                    for (var i in data.issues) {
                        var child = data.issues[i];
                        child.fields.worklog = {total: 0, maxResults: 0, worklogs: []}; // we need numeric fields only
                        var $subtaskChildren = /* nullable */ service.processChildren(pivotTable, child, options, previousIssues);
                        subtasksPromises.push($subtaskChildren);
                        $q.when($subtaskChildren).then((function(child) {
                            return function() {
                                service.addWorklogs(child, issue); // sum numeric fields
                            }
                        })(child)); // closure in loop
                    }
                });
                promise.then(function() { // wait for all children
                    $q.all(subtasksPromises).then(subtasksDeferred.resolve);
                });
            } else {
                subtasksDeferred.resolve();
            }
            return subtasksDeferred.promise;
        }
        return epicPromise;
    };

    this.checkIfMatches = function(issue, options) {
        if (!this.matches) { // for tests
            if (options.pivotTableType == 'TimeTracking') {
                this.matches = $q.resolve();
            } else {
                this.initMatches(options);
            }
        }
        var deferred = $q.defer();
        this.matches.then(function(/*array of arrays of issues */) {
            var m = this.matchIssueIds == null;
            m = m && (options.projectKeys == null || options.projectKeys.length == 0 || options.projectKeys.indexOf(issue.fields.project.key) >= 0);
            m = m || (this.matchIssueIds != null && this.matchIssueIds.indexOf(issue.id) != -1);
            deferred.resolve(m);
        }.bind(this), deferred.reject);
        return deferred.promise;
    };

    this.addOnceIfMatches = function(pivotTable, issue, options) {
        var self = this;
        var match = pivotTable.matches[issue.key];
        if (!match) {
            match = pivotTable.matches[issue.key] = $q.defer();
            this.checkIfMatches(issue, options).then(function(matches) {
                if (matches) {
                    var promises = [];
                    self.getEpicFields(pivotTable, issue, options, promises);
                    var promise = /* nullable */ self.processChildren(pivotTable, issue, options, []);
                    promises.push(promise);
                    $q.all(promises).then(function() {
                        var promiseWorklog = self.loadAllWorklogs(issue, null, options).promise;
                        var promiseChangelog = self.loadAllChangelogs(issue, options).promise;
                        $q.all([promiseWorklog, promiseChangelog]).then(function () {
                            // queue for addition issue that was finally resolved with all changelogs, worklogs and filtered
                            pivotTable.queueToAdd.push(issue);
                            match.resolve(issue);
                        });
                    });
                } else {
                     // not queued for add
                    match.resolve();
                }
            });
        }
        return match.promise;
    };

    // add worklogs to highest parent
    this.addWorklogsToParent = function(pivotTable, issue, deferred, options, parent) {
        var pivottableService = this;
        this.onParentIssue(pivotTable, issue, options, parent, [], function(data) {
            if (!data) {
                data = issue; // cycle
            }
            // load all parent worklogs before adding child worklogs
            pivottableService.addOnceIfMatches(pivotTable, data, options).then(
                function(matches) { // parent with all worklogs loaded
                    if (matches && issue.key != data.key) {
                        pivottableService.addWorklogs(issue, data);
                    }
                    deferred.resolve();
                });
        });
        return deferred.promise;
    };

    this.onParentIssue = function(pivotTable, issue, options, parent, previous, callback) {
        var pivottableService = this;
        parent.promise.then(function(data) {
            if (!data) { // jiratimesheet issue#1000 parent issue not accessible
                logger.debug("Parent issue for " + issue.key + " is not visible");
                callback(issue); // last visible parent
            } else if (previous.indexOf(data.key) >= 0) {
                logger.debug("Cycle summing subtasks for issue " + issue.key, previous);
                callback();
            } else {
                previous.push(data.key);
                var result = pivottableService.getParentIssue(pivotTable, data, options);
                if (result.hasOwnProperty('promise')) {
                    pivottableService.onParentIssue(pivotTable, issue, options, result, previous, callback);
                } else {
                    callback(data);
                }
            }
        });
    };

    this.addWorklogs = function(source, target) {
        var sourceWorklog = source.worklog || source.fields.worklog;
        var targetWorklog = target.worklog || target.fields.worklog;
        var issue = {key: source.key, fields: {summary: source.fields.summary}};
        // merge worklogs
        for (var i in sourceWorklog.worklogs) {
            sourceWorklog.worklogs[i]._issue = issue;
            targetWorklog.worklogs.push(sourceWorklog.worklogs[i]);
        }

        // sum numeric fields only once
        if (!target._subTaskIds) {
            target._subTaskIds = [];
        }
        var sourceId = parseInt(source.id);
        if (target._subTaskIds.indexOf(sourceId) >= 0) {
            return; // already added by processChildren or by processIssues
        } else {
            target._subTaskIds.push(sourceId);
        }
        target.fields.timeestimate = (source.fields.timeestimate || 0) + (target.fields.timeestimate || 0);
        target.fields.timeoriginalestimate = (source.fields.timeoriginalestimate || 0) + (target.fields.timeoriginalestimate || 0);
        target.fields.timespent = (source.fields.timespent || 0) + (target.fields.timespent || 0);
        // TODO: sum all numeric fields!
    };

    this.restrictedByAuditorsGroups = function(loggedInUser) {
        var deferred = $q.defer();
        configurationService.getConfiguration().then(function (config) {
            var auditorsGroups = config.auditorsGroups.val;
            var restricted = auditorsGroups != null; // for empty auditors groups
            if (restricted) {
                for (var i = 0; i < loggedInUser.groups.items.length && restricted; i++) {
                    restricted = auditorsGroups.indexOf(loggedInUser.groups.items[i].name) == -1;
                }
            }
            deferred.resolve(restricted);
        });
        return deferred.promise;
    };

    this.getPermissions = function(query) {
        if (!this.permissions.hasOwnProperty(query)) {
            var deferred = $q.defer();
            this.permissions[query] = deferred.promise;
            var promises = this.getPermissionsPromises;
            var previous =  promises.length > MAX_BULK_LENGTH ? promises[promises.length - MAX_BULK_LENGTH] : 0;
            promises.push(deferred.promise);
            var service = this;
            $q.when(previous).then(function() { // not more than MAX_BULK_LENGTH concurrent requests
                AP.request({
                    url: '/rest/api/2/mypermissions' + query,
                    dataType: 'json',
                    success: function (data) {
                        data = TimesheetUtils.getJson(data);
                        deferred.resolve(data.permissions);
                    },
                    error: function () {
                        var args = Array.prototype.slice.call(arguments);
                        args.push(query, deferred);
                        service.onError.apply(service, args);
                    }
                });
            });
        }
        return this.permissions[query];
    };

    this.havePermissions = function(permissionsReq /*, 'projectKey=DEMO', 'issueKey=DEMO-1' */) {
        var query = '?' + [].slice.call(arguments, 1).join('&');
        return this.getPermissions(query).then(function (permissions) {
            var res = [];
            for (var  i = 0; i < permissionsReq.length; i++) {
                res.push(permissions[permissionsReq[i]] != null && permissions[permissionsReq[i]].havePermission == true);
            }
            return res;
        });
    };

    this.isAdministrator = function() {
        return this.havePermissions(['ADMINISTER']).then(function(permissions) {
            return permissions[0];
        });
    };

    this.isProjectAdmin = function (projectKey) {
        return this.havePermissions(['PROJECT_ADMIN'], 'projectKey=' + projectKey).then(function(permissions) {
            return permissions[0];
        });
    };

    this.findIssueByKey = function (key, options, deferred) {
        var promises = this.findIssueByKeyPromises;
        var previous =  promises.length > MAX_BULK_LENGTH ? promises[promises.length - MAX_BULK_LENGTH] : 0;
        if (!deferred) {
            deferred = $q.defer();
        }
        promises.push(deferred.promise);
        var service = this;
        $q.when(previous).then(function() { // not more than MAX_BULK_LENGTH concurrent requests
            if (!deferred.promise.$$state || deferred.promise.$$state.status == 0) {
                var url = '/rest/api/2/issue/' + key + '?fields=' + options._fields;
                if (['IssueWorkedTimeByStatus', 'IssuePassedTimeByStatus'].indexOf(options.pivotTableType) >= 0) {
                    url += '&expand=changelog';
                }
                AP.request({
                    url: url,
                    dataType: 'json',
                    success: function(data) {
                        var issue = TimesheetUtils.getJson(data);
                        if (issue.fields && !issue.fields.worklog) {
                            issue.fields.worklog = {total: 0, maxResults: 0, worklogs: []};
                        }
                        deferred.resolve(issue);
                    },
                    error: function(jqXHR) {
                        if ([403, 404].indexOf(jqXHR.status) >= 0) {
                            deferred.resolve(); // jiratimesheet issue#1000/issue#1164 issue not accessible
                        } else {
                            var args = Array.prototype.slice.call(arguments);
                            args.push(url, deferred);
                            service.onError.apply(service, args);
                        }
                    }
                });
            }
        });
        return deferred.promise;
    };

    this.getIssueWorklog = function(issueKey, issueId) {
        if (this.cacheWorklog) {
            var issueWorklog = window.localStorage.getItem('i' + issueId);
            if (issueWorklog) {
                this.updateCacheExpiry(function(cacheExpiry) {
                    cacheExpiry[issueId] = logger._; // session id
                });
                return $q.resolve(TimesheetUtils.getJson(LZString.decompressFromUTF16(issueWorklog)));
            }
        }
        var deferred = $q.defer();
        var previous = this.issueWorklogs.length > MAX_BULK_LENGTH ? this.issueWorklogs[this.issueWorklogs.length - MAX_BULK_LENGTH] : 0;
        this.issueWorklogs.push(deferred.promise);
        var service = this;
        $q.when(previous).then(function() { // not more than MAX_BULK_LENGTH concurrent requests
            AP.request({
                url: '/rest/api/2/issue/' + issueKey + '/worklog',
                dataType: 'json',
                success: function(data) {
                    var worklog = TimesheetUtils.getJson(data);
                    if (service.cacheWorklog) try {
                        window.localStorage.setItem('i' + issueId, LZString.compressToUTF16(data)); // JSON string
                        worklog.worklogs.forEach(function(entry) {
                            window.localStorage.setItem('w' + entry.id, issueId);
                        });
                        service.updateCacheExpiry(function(cacheExpiry) {
                            cacheExpiry[issueId] = logger._; // session id
                        });
                    } catch (err) {
                        // TODO: expire old data and try again
                        console.log('cache is full', issueId, err);
                    };
                    deferred.resolve(worklog);
                },
                error: function () {
                    deferred.reject();
                }
            });
        });
        return deferred.promise;
    };

    this.filterWorklogs = function(issue, deferred, options) {
        var promises = [];
        var service = this;
        var issueWorklog = issue.worklog || issue.fields.worklog;
        if (issueWorklog.worklogs && issueWorklog.worklogs.length > 0) {
            angular.forEach(issueWorklog.worklogs, function(worklog) {
                var tags = TimesheetUtils.getTags(worklog.comment);
                if (tags && tags.length > 0) {
                    angular.forEach(tags, function(tag) {
                        if (service.workLabels[tag]) {
                            service.workLabels[tag]++;
                        } else {
                            service.workLabels[tag] = 1;
                        }
                    });
                }
                var isAuthorObject = angular.isObject(worklog.author);
                if ((service.groups && !options.configOptions.storeWorklog) || !isAuthorObject) {
                    var username = worklog.author ? worklog.author.key || worklog.author.name || worklog.author : ''; // key || name
                    if (!service.userGroups.hasOwnProperty(username)) {
                        var userGroups = service.userGroups[username] = service.getUserInfo(username);
                        promises.push(userGroups);
                        if (service.groups) {
                            userGroups.then(service.initWorklogAuthors.bind(service));
                        }
                    } else if (!service.userGroups[username].$$state
                                || service.userGroups[username].$$state.status == 0) {
                        // wait for previous call (condition may be safely removed, if causes problems)
                        promises.push(service.userGroups[username]);
                    }
                    if (!isAuthorObject) {
                        service.userGroups[username].then(function (user) {
                            worklog.author = user;
                        });
                    }
                }
            });
        }

        if (promises.length == 0 && !deferred) {
            return issue;
        }
        var deferred = deferred || $q.defer();
        $q.all(promises).then(function() {
            deferred.resolve(issue);
        });
        return deferred;
    };

    this.initWorklogAuthors = function (user) {
        if (user) {
            for (var i = 0; i < user.groups.items.length; i++) {
                if (this.groups.indexOf(user.groups.items[i].name) >= 0) {
                    this.worklogAuthors.push(user.key);
                    break;
                }
            }
        } // else deleted
    };

    this.loadAllWorklogs = function(issue, deferred, options) {
        var service = this;
        var worklog = issue.worklog || issue.fields.worklog;
        if (!worklog) {
            worklog = issue.fields.worklog = {total: 1, maxResults: 0, worklogs: []}; // force load worklog
        }
        if (worklog.total > worklog.maxResults) {
            var def = deferred || $q.defer();
            var copyAndFilterWorklogs = function(data) {
                angular.copy(data, worklog);
                service.filterWorklogs(issue, def, options);
                return data;
            };
            this.getIssueWorklog(issue.key, issue.id).then(copyAndFilterWorklogs, def.reject);
            return def;
        } else {
            // all were loaded
            return this.filterWorklogs(issue, deferred, options); // issue or deferred (new or existing)
        }
    };

    this.getIssueChangelog = function (issue, startAt, nextPage) {
        var deferred = $q.defer();
        var self = this;
        var url = nextPage || '/rest/api/2/issue/' + issue.key + '/changelog?startAt=' + startAt;
        AP.request({
            url: url,
            dataType: 'json',
            success: function (data) {
                var log = TimesheetUtils.getJson(data);
                issue.changelog.histories = issue.changelog.histories.concat(log.values);
                if (log.isLast) {
                    deferred.resolve(issue);
                } else {
                    self.getIssueChangelog(issue, log.startAt + log.maxResult, log.nextPage).then(deferred.resolve, deferred.reject);
                }
            },
            error: function () {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                self.onError.apply(self, args);
            }
        });
        return deferred.promise;
    };

    this.loadAllChangelogs = function (issue, options) {
        var changelog = issue.changelog;
        if (!changelog) {
            return {};
        }
        if (changelog.total > changelog.maxResults) {
            var def = $q.defer();
            var self = this;
            var previous = this.issueChangelogs.length > MAX_BULK_LENGTH ? this.issueChangelogs[this.issueChangelogs.length - MAX_BULK_LENGTH] : 0;
            this.issueChangelogs.push(def.promise);
            $q.when(previous).then(function() {
                self.getIssueChangelog(issue, changelog.maxResults).then(def.resolve, def.reject);
            });
            return def;
        }
        return {};
    };

    this.getMoreFields = function (options) {
        var moreFields = options.moreFields && angular.isArray(options.moreFields) ? options.moreFields.slice() : [];
        if (options.groupByField) moreFields.unshift(options.groupByField);
        if (options.categorizeByField) moreFields.unshift(options.categorizeByField);
        return moreFields;
    };

    this.getEpicFields = function(pivotTable, issue, options, promises) {
        var moreFields = this.getMoreFields(options);
        for (var i = 0; i < moreFields.length; i++) {
            var moreField = moreFields[i];
            if (this.isEpicField(moreField)) {
                var promise = this.getEpic(pivotTable, issue, options);
                if (promise) {
                    promises.push(promise);
                    promise.then((function(moreField, epicLinkField) { // closure inside loop!
                        return function(epic) {
                            if (epic) {
                                issue.fields[moreField] = epicLinkField ? epic.key : epic.fields[moreField];
                            }
                        }
                    })(moreField/*, moreField == this.epicLinkField.id*/));
                    // uncoment and add gh-epic-label below in isEpicField()
                    // when Lawrence Lachman wants to include sub-tasks
                }
            }
        }
    };

    this.isEpicField = function(fieldName) {
        if (!this.epicFields.hasOwnProperty(fieldName)) {
            for (var i = 0; i < this.allFields.length; i++) {
                var field = this.allFields[i];
                if (field.id == fieldName) {
                    return this.epicFields[fieldName] = field.custom && field.schema != null
                        // TODO: more epic field
                        && ["com.pyxis.greenhopper.jira:gh-epic-color",
                        "com.pyxis.greenhopper.jira:gh-epic-status",
                        "com.pyxis.greenhopper.jira:gh-epic-label"].indexOf(field.schema.custom) >= 0;
                }
            }
            this.epicFields[fieldName] = false;
        }
        return this.epicFields[fieldName];
    };

    this.initEpicLinkField = function() {
        if (!this.hasOwnProperty('epicLinkField')) {
            this.epicLinkField = null;
            for (var i = 0; i < this.allFields.length; i++) {
                var field = this.allFields[i];
                if (field.custom && field.schema != null && field.schema.custom == "com.pyxis.greenhopper.jira:gh-epic-link") {
                    this.epicLinkField = field;
                    break;
                }
            }
        }
    };

    this.getEpic = function(pivotTable, issue, options) {

        if (!this.epicLinkField) return null;

        var result = this.getParentIssue(pivotTable, issue, options);
        if (result.hasOwnProperty('promise')) {
            var deferred = $q.defer();
            var pivottableService = this;
            this.onParentIssue(pivotTable, issue, options, result, [], function(data) {
                if (data && data.fields.issuetype.name == 'Epic') { // Parent Issue Field configured
                    deferred.resolve(data);
                } else if (data && data.fields[pivottableService.epicLinkField.id]) {
                    pivottableService.getParentIssueByKey(pivotTable, issue, data.fields[pivottableService.epicLinkField.id], options).promise.then(deferred.resolve);
                } else {
                    deferred.resolve();
                }
            });
            return deferred.promise;
        } else if (issue.fields[this.epicLinkField.id]) {
            return this.getParentIssueByKey(pivotTable, issue, issue.fields[this.epicLinkField.id], options).promise;
        } else {
            return null;
        }
    };

    this.onError = function(jqXHR, type, error, url, deferred) {
        if (jqXHR.status == 401) {
            this.loginExpired();
        } else if ([500, 502, 503, 504].indexOf(jqXHR.status) >= 0) {
            this.serviceUnavailable();
        } else if (jqXHR.status == 403) {
            this.forbidden();
        } else {
            this.unexpectedError(jqXHR, error, url);
        }
        deferred.reject();
    };

    this.loginExpired = function() {
        AP.flag.create({
            title: 'Jira session expired',
            body: 'Please login again to restore connection with Jira',
            type: 'error'
        });
    };

    this.serviceUnavailable = function() {
        AP.flag.create({
            title: 'Jira Unavailable',
            body: 'The requested resource failed to load. If the problem persists, please contact Atlassian Cloud Support.',
            type: 'error'
        });
    };

    this.forbidden = function() {
        AP.flag.create({
            title: 'Forbidden',
            body: 'You need to accept WRITE and DELETE scopes for the add-on in UPM',
            type: 'error'
        });
    };

    this.unexpectedError = function(jqXHR, error, cause) {
        var body = '';
        if (jqXHR && jqXHR.responseText) {
            try {
                var data = TimesheetUtils.getJson(jqXHR.responseText);
                body = data.errorMessages ? data.errorMessages.join(' ') : 'Unknown';
                if (data.errors) {
                    body += '\n' + JSON.stringify(data.errors);
                }
                data.status = jqXHR.status;
                // FIXME: status == 400 || 404
                /* it logs too many errors, reasons of which are still not clear, e.g.:
                Issue Does Not Exist - /rest/api/2/issue/undefined/worklog/undefined?adjustEstimate=auto
                A value with ID \'22854\' does not exist for the field \'filter\' - /rest/api/2/search?...jql=filter=22854...
                Cannot find worklog with id: \'69756\' -
                Worklog must not be null - timeLogged: 'Invalid time duration entered -
                */
                //logger.error(data, cause);
            } catch(err) {
                function strip(html) {
                   var tmp = document.createElement("DIV");
                   tmp.innerHTML = html;
                   return tmp.textContent || tmp.innerText || "";
                }
                body = cause + ' ' + jqXHR.status + ' ' + strip(jqXHR.responseText);
                //logger.error(jqXHR.status + ' ' + body, cause);
            }
        } else {
            body = error;
            // it logs too many useless errors for straight forward REST calls
            //logger.error(error, cause);
        }
        AP.flag.create({
            title: 'Unexpected error',
            body: body,
            type: 'error'
        });
    };

    this.getGadgetConfiguration = function(dashboardId, dashboardItemId) {
        var deferred = $q.defer();
        AP.request({
            url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties',
            success: function(data) {
                data = TimesheetUtils.getJson(data);
                if (data.keys.length > 0) { // assume it has DASHBOARD_PROPERTY_GADGET_CONFIG
                    AP.request({
                        url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties/' + DASHBOARD_PROPERTY_GADGET_CONFIG,
                        success: function(data) {
                            data = TimesheetUtils.getJson(data);
                            deferred.resolve(data.value);
                        },
                        error: function(e) {
                            deferred.resolve({});
                        }
                    });
                } else {
                    deferred.resolve({});
                }
            },
            error: function(e) {
                deferred.resolve({});
            }
        });
        return deferred.promise;
    };

    this.saveGadgetConfiguration = function(dashboardId, dashboardItemId, configuration) {
        var deferred = $q.defer();
        AP.request({
            url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties/' + DASHBOARD_PROPERTY_GADGET_CONFIG,
            type: 'PUT',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            data: JSON.stringify(configuration),
            success: function(data) {
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function(e){
                deferred.reject();
            }
        });
        return deferred.promise;
    };

    this.createWorklog = function(worklog) {
        var deferred = $q.defer();
        var service = this;
        var params = this.getEstimateParams(worklog, 'reduceBy');
        var url = '/rest/api/2/issue/' + worklog.issueKey + '/worklog' + params;
        AP.request({
            url: url,
            type: 'POST',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            data: service.getCreateWorklogRequest(worklog),
            success: function(data) {
                data = TimesheetUtils.getJson(data)
                service.removeWorklogFromCache(data); // is also removed by clearWorklogCache but late
                deferred.resolve(data);
            },
            error: function() {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.getWorklog = function(issueKey, worklogId) {
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/issue/' + issueKey + '/worklog/' + worklogId;
        AP.request({
            url: url,
            type: 'GET',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            success: function(data) {
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function() {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.updateWorklog = function(worklog) {
        var deferred = $q.defer();
        var service = this;
        var params = this.getEstimateParams(worklog);
        var url = '/rest/api/2/issue/' + worklog.issueKey + '/worklog/' + worklog.id + params;
        AP.request({
            url: url,
            type: 'PUT',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            data: service.getUpdateWorklogRequest(worklog),
            success: function(data) {
                service.removeWorklogFromCache(worklog);  // is also removed by clearWorklogCache but late
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function() {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.deleteWorklog = function(worklog) {
        var deferred = $q.defer();
        var service = this;
        var params = this.getEstimateParams(worklog, 'increaseBy');
        var url = '/rest/api/2/issue/' + worklog.issueKey + '/worklog/' + worklog.id + params;
        AP.request({
            url: url,
            type: 'DELETE',
            contentType: 'application/json',
            success: function() {
                service.removeWorklogFromCache(worklog);  // is also removed by clearWorklogCache but late
                deferred.resolve(true);
            },
            error: function(e){
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.getEstimateParams = function (worklog, adjustmentType) {
        var params = "?adjustEstimate=" + worklog.estimateType;
        if (worklog.estimateType == 'new') {
            params += "&newEstimate=" + encodeURIComponent(worklog.estimateValue);
        } else if (worklog.estimateType == 'manual') {
            // delete worklog only
            params += "&" + adjustmentType + "=" + encodeURIComponent(worklog.adjustmentAmount);
        }
        return params;
    };

    this.getCreateWorklogRequest = function(worklog) {
        return JSON.stringify({
            comment: worklog.comment,
            started: TimesheetUtils.formatDate(worklog.date),
            timeSpent: worklog.timeSpent,
            visibility: worklog.visibilityGroup ?
                {type: 'group', value: worklog.visibilityGroup} : worklog.visibilityRole ?
                    {type: 'role', value: worklog.visibilityRole} : null
        });
    };

    this.getUpdateWorklogRequest = function(worklog) {
        return JSON.stringify({
            comment: worklog.comment,
            started: worklog.started,
            timeSpent: worklog.timeSpent,
            visibility: worklog.visibilityGroup ?
                {type: 'group', value: worklog.visibilityGroup} : worklog.visibilityRole ?
                    {type: 'role', value: worklog.visibilityRole} : null
        });
    };

    this.onDeletedWorklogs = function(since, callback) {
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/worklog/deleted?since=' + since;
        AP.request({
            url: url,
            type: 'GET',
            contentType: 'application/json',
            success: function(data) {
                var deletedWorklogs = TimesheetUtils.getJson(data);
                callback(deletedWorklogs);
                if (!deletedWorklogs.lastPage) {
                    service.onDeletedWorklogs(deletedWorklogs.until, callback).then(deferred.resolve);
                } else {
                    deferred.resolve(deletedWorklogs.until + 1); // avoid same last worklog next time
                }
            },
            error: function(e){
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.onUpdatedWorklogs = function(since, callback) {
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/worklog/updated?since=' + since;
        AP.request({
            url: url,
            type: 'GET',
            contentType: 'application/json',
            success: function(data) {
                callback(TimesheetUtils.getJson(data)).then(function(updatedWorklogs) {
                    if (!updatedWorklogs.lastPage) {
                        service.onOutdatedWorklogs(updatedWorklogs.until, callback).then(deferred.resolve);
                    } else {
                        deferred.resolve(updatedWorklogs.until + 1); // avoid same last worklog next time
                    }
                });
            },
            error: function(e){
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.onWorklogs = function(updatedWorklogs, callback) {
        var worklogIds = updatedWorklogs.values.map(function (worklog) {
          return worklog.worklogId;
        });
        if (!worklogIds.length) {
            return $q.resolve(updatedWorklogs);
        }
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/worklog/list';
        AP.request({
            url: url,
            type: 'POST',
            contentType: 'application/json',
            data: JSON.stringify({ids: worklogIds}),
            success: function(data) {
                callback(TimesheetUtils.getJson(data));
                deferred.resolve(updatedWorklogs);
            },
            error: function(e){
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.removeWorklogFromCache = function(worklog) {
        if (this.cacheWorklog) try {
            var issueId = worklog.issueId || window.localStorage.getItem('w' + worklog.worklogId);
            var i = window.localStorage.getItem('i' + issueId);
            if (i) {
                var issueWorklog = TimesheetUtils.getJson(LZString.decompressFromUTF16(i));
                issueWorklog.worklogs.forEach(function(entry) {
                    window.localStorage.removeItem('w' + entry.id);
                });
                window.localStorage.removeItem('i' + issueId);
                this.updateCacheExpiry(function(cacheExpiry) {
                    delete cacheExpiry[issueId];
                });
            } else if (worklog.id || worklog.worklogId) { // cacheExpiry
                window.localStorage.removeItem('w' + (worklog.id || worklog.worklogId));
            }
        } catch (err) {
            console.log('Failed to remove worklog ' + (worklog.id || worklog.worklogId || 'for issue ' + worklog.issueId) + ' from cache', err);
        };
    };

    var CACHE_EXPIRY = 'cacheExpiry';

    this.updateCacheExpiry = function(callback) {
        try {
            var cacheExpiry = TimesheetUtils.getJson(window.localStorage.getItem(CACHE_EXPIRY)) || {};
            callback(cacheExpiry);
            window.localStorage.setItem(CACHE_EXPIRY, JSON.stringify(cacheExpiry));
        } catch (err) {
            console.log('Failed to update cache expiry', err);
        }
    };

    this.clearWorklogCache = function(config) {
        this.cacheWorklog = config.cacheWorklog && config.cacheWorklog.val
                && (!config.storeWorklog || !config.storeWorklog.val);
        if (this.cacheWorklog) try {
            var cacheId = window.localStorage.getItem('cacheId');
            var id = logger.baseUrl + '#0';
            if (cacheId != id) {
                window.localStorage.clear();
                window.localStorage.setItem('cacheId', id);
            }
            var service = this;
            var updatedSince = window.localStorage.getItem('updatedSince');
            var deletedSince = window.localStorage.getItem('deletedSince');
            var $updatedSince;
            var defaultSince = Date.now() - 3600000; // -1 minute
            if (!updatedSince) {
                $updatedSince = $q.resolve(defaultSince);
            } else {
                $updatedSince = this.onUpdatedWorklogs(parseInt(updatedSince), function(data) {
                    return service.onWorklogs(data, function(worklogs) {
                        worklogs.forEach(service.removeWorklogFromCache.bind(service));
                    });
                });
            }
            var $deletedSince;
            if (!deletedSince) {
                $deletedSince = $q.resolve(defaultSince);
            } else {
                $deletedSince = this.onDeletedWorklogs(parseInt(deletedSince), function(deletedWorklogs) {
                    deletedWorklogs.values.forEach(service.removeWorklogFromCache.bind(service));
                });
            }
            // FIXME: does not include changes during last minute
            return $q.all([$updatedSince, $deletedSince]).then(function(untils) {
                window.localStorage.setItem('updatedSince', untils[0]);
                window.localStorage.setItem('deletedSince', untils[1]);
                });
        } catch (err) {
            console.log('Failed to clear worklog cache', err);
        };
    };

    this.expireWorklogCache = function() {
        if (this.cacheWorklog) {
            var cacheExpiry = TimesheetUtils.getJson(window.localStorage.getItem(CACHE_EXPIRY));
            if (cacheExpiry) {
                for (var key in cacheExpiry) {
                    if (logger._ - cacheExpiry[key] > 3600000*24*7) { // 1w
                        this.removeWorklogFromCache({issueId: key});
                    }
                }
            }
        }
    };

    this.resetState = function() {
        this.allIssues = null;
        this.allFilters = null;
        this.allProjects = null;
        this.allFields = null;
        this.filterOrProjectId = null;
        this.sumSubTasks = null;
        this.includeEmpty = null;
        this.permissions = {};
    };

    this.checkAndUpdateOptions = function(options) {
        var moreFields = this.getMoreFields(options);
        var changed = this.allIssues == null
            || !angular.equals(this.filterOrProjectId, options.filterOrProjectId)
            || this.sumSubTasks != options.sumSubTasks
            || !this.includeEmpty && options.includeEmpty // reload if includeEmpty
            || !angular.equals(this.startDate, options.startDate)
            || !angular.equals(this.endDate, options.endDate)
            || this.username != options.username
            || this.groups != options.groups
            || (this.pivotTableType != 'IssueWorkedTimeByStatus' && options.pivotTableType == 'IssueWorkedTimeByStatus') // reload with changelog
            || (this.pivotTableType != 'IssuePassedTimeByStatus' && options.pivotTableType == 'IssuePassedTimeByStatus') // reload with changelog
            || !angular.equals(this.moreFields, moreFields);

        if (changed) {
            this.allIssues = [];
            this.filterOrProjectId = angular.copy(options.filterOrProjectId);
            this.sumSubTasks = options.sumSubTasks;
            this.includeEmpty = options.includeEmpty;
            this.startDate = options.startDate;
            this.endDate = options.endDate;
            this.username = options.username;
            this.groups = options.groups;
            this.userGroups = {};
            this.worklogAuthors.splice(0, this.worklogAuthors.length);
            this.matches = null; // checkIfMatches
            this.pivotTableType = options.pivotTableType;
            this.moreFields = moreFields;
            this.issueWorklogs.splice(0, this.issueWorklogs.length);
            this.issueChangelogs.splice(0, this.issueChangelogs.length);
            this.findIssueByKeyPromises.splice(0, this.findIssueByKeyPromises.length);
            this.getPermissionsPromises.splice(0, this.getPermissionsPromises.length);
        }
        return changed;
    };

    this.addDays = function(stringDate, days) {
        if (stringDate == null || stringDate == "") {
            return stringDate;
        }
        var date = TimesheetUtils.addDays(new Date(stringDate), days);
        return TimesheetUtils.formatDateForSearch(date);
    };
}]);
