app.controller('WorklogController', ['$scope', '$route', '$routeParams', '$location', '$sce', '$timeout', 'loggedInUser',
       'pivottableService', 'applicationLoggingService', 'configurationService',
    function ($scope, $route, $routeParams, $location, $sce, $timeout, loggedInUser,
        pivottableService, logger, configurationService) {

        var AP = window.RA || window.AP;

        init();

        function init() {
            AP.resize();

            var calculateWorklogDate = function() {
                var newStart = moment();
                if ($scope.worklogDatetime.date != null) {
                    newStart = moment($scope.worklogDatetime.date).startOf('day');
                    if ($scope.worklogDatetime.time != null) {
                        $scope.worklogDatetime.time.timePartsToMoment();
                        newStart.add($scope.worklogDatetime.time.momentTime);
                    }
                } else if ($scope.worklog.started != null) {
                    newStart = moment($scope.worklog.started);
                }
                return newStart.toDate();
            };

            var populateDatetime = function (datetimeObj, dateString, timezone) {
                var momentDate = moment(TimesheetUtils.convertDate(dateString, timezone));
                datetimeObj.date = momentDate.toDate();
                datetimeObj.time.momentTime = momentDate;
                datetimeObj.time.momentToTimeParts();
            };

            var deleteWorklogFunction = function() {
                $scope.$apply(function() {
                    $scope.loading = true;
                });
                pivottableService.deleteWorklog($scope.worklog)
                    .then(function(worklog) {
                        AP.dialog.close({
                            action: 'deleted',
                            worklogOld: $scope.worklog,
                            issueKey: $scope.worklog.issueKey
                        });
                    });
            };
            var updateWorklogFunction = function() {
                $scope.$apply(function() {
                    $scope.loading = true;
                });
                var worklogOld = angular.copy($scope.worklog);
                $scope.worklog.started = TimesheetUtils.formatDate(calculateWorklogDate());
                pivottableService.updateWorklog($scope.worklog)
                    .then(function(worklog) {
                        AP.dialog.close({
                            action: 'updated',
                            worklogOld: worklogOld,
                            worklog: worklog,
                            issueKey: $scope.worklog.issueKey
                        });
                    });
            };
            var createWorklogFunction = function() {
                $scope.$apply(function() {
                    $scope.loading = true;
                });
                $scope.worklog.date = calculateWorklogDate();
                pivottableService.createWorklog($scope.worklog)
                    .then(function(worklog) {
                        AP.dialog.close({
                            action: 'added',
                            worklog: worklog,
                            issueKey: $scope.worklog.issueKey
                        });
                    });
            };

            var getSubmitHandler = function(dialogType) {
                switch (dialogType) {
                    case 'delete':
                        return deleteWorklogFunction;
                    case 'update':
                        return updateWorklogFunction;
                    case 'create':
                    case 'add':
                        return createWorklogFunction;
                    default:
                        throw new Error('Unsupported dialogType: ' + dialogType);
                }
            };

            $scope.contentTemplate = function() {
                return $scope.templates['dialog']; // furl
            };

            $scope.worklog = {
                estimateType: 'auto',
                estimateValue: ''
            };

            $scope.$watch('worklog.estimateType', function(estimateType) {
                $scope.worklog.estimateValue = '';
                $scope.worklog.adjustmentAmount = '';
            });

            configurationService.getConfiguration().then(function(config) {
                $scope.workLabelsList = config.workLabels.val;
                $scope.workDescriptionRequired = config.workDescriptionRequired.val;
                $scope.visibilityGroup = config.worklogVisibilityGroup.val;
                $scope.visibilityRole = config.worklogVisibilityRole.val;
                pivottableService.clearWorklogCache(config);
            });

            $scope.worklogDatetime = {
                date: "",
                time: {
                    momentTime: null,
                    timeParts: [],
                    momentToTimeParts: function() {
                        if (this.momentTime == null) return;
                        var regExp = /^(.*?)(\d+):(\d+)(.*?)$/;
                        var match = regExp.exec(this.momentTime.format('LT'));
                        if (!match) {
                            match = regExp.exec(this.momentTime.locale('en').format('LT'));
                        }
                        var items = [];
                        var format24 = match[1] == "" && match[4] == "";
                        if (match[1] != "") {
                            items.push({
                                value: match[1],
                                step: 12
                            });
                        }
                        items.push({
                            value: match[2],
                            step: 1,
                            min: format24 ? 0 : 1,
                            max: format24 ? 23 : 12,
                            suffix: ":"
                        });
                        items.push({
                            value: match[3],
                            step: 5,
                            min: 0,
                            max: 59
                        });
                        if (match[4] != "") {
                            items.push({
                                value: match[4],
                                step: 12
                            });
                        }
                        this.timeParts = items;
                    },
                    timePartsToMoment: function() {
                        if (this.timeParts != null) {
                            var time = "";
                            for (var i = 0; i < this.timeParts.length; i++) {
                                time += this.timeParts[i].value;
                                if (this.timeParts[i].suffix != null) {
                                    time += this.timeParts[i].suffix;
                                }
                            }
                            this.momentTime = moment(time, 'LT');
                        }
                    }
                }
            };

            $scope.groups = loggedInUser.groups;

            AP.dialog.getCustomData(function(params) {
                if ($scope.dialogType != 'delete') {
                    $scope.$watch('worklog.issueKey', function(newValue, oldValue) {
                        if (newValue) {
                            var projectKey = newValue.split('-')[0];
                            pivottableService.isProjectAdmin(projectKey).then(function (isAdmin) { // FIXME: ACJIRA-424
                                if (isAdmin) { // avoid error in console
                                    configurationService.getRoles(projectKey).then(function (roles) {
                                        delete roles['atlassian-addons-project-access'];
                                        $scope.roles = roles;
                                    });
                                } else {
                                    $scope.roles = null;
                                }
                            });
                        }
                    });
                }
                if (params.worklogId != null) {
                    pivottableService.getWorklog(params.issueKey, params.worklogId).then(function (worklog) {
                        worklog.issueKey = params.issueKey;
                        worklog.visibilityGroup = worklog.visibility && worklog.visibility.type == 'group'
                            ? worklog.visibility.value : '';
                        worklog.visibilityRole = worklog.visibility && worklog.visibility.type == 'role'
                            ? worklog.visibility.value : '';
                        angular.extend($scope.worklog, worklog);
                        var timeZone = worklog.author ? worklog.author.timeZone : null;
                        populateDatetime($scope.worklogDatetime, worklog.started, timeZone);
                    });
                } else {
                    $scope.$watch('visibilityGroup', function(newValue, oldValue) {
                        if (newValue) {
                            var visibilityGroups = newValue.split(',');
                            for (var i = 0; i < $scope.groups.length; i++) {
                                var group = $scope.groups[i].name;
                                if (visibilityGroups.indexOf(group) >= 0) {
                                    $scope.worklog.visibilityGroup = group;
                                    break;
                                }
                            }
                        }
                    });
                    $scope.$watch('visibilityRole', function(newValue, oldValue) {
                        if (newValue) {
                            var visibilityRoles = newValue.split(',');
                            $scope.$watch('roles', function(roles) {
                                if (roles) {
                                    for (var role in roles) {
                                        if (visibilityRoles.indexOf(role) >= 0) {
                                            $scope.worklog.visibilityRole = role;
                                            break;
                                        }
                                    }
                                } else {
                                    $scope.worklog.visibilityRole = null;
                                }
                            });
                        }
                    });
                    angular.extend($scope.worklog, {timeSpent: params.timeSpent, comment: params.comment, issueKey: params.issueKey});
                    var jsonDate = TimesheetUtils.formatDate(params.date != null ? params.date : new Date());
                    populateDatetime($scope.worklogDatetime, jsonDate, null);
                    $scope.$apply();
                }
                if (params.initialWorkLabels) {
                    var labels = Object.keys(params.initialWorkLabels);
                    $scope.workLabelsList =
                        $scope.workLabelsList
                            ? TimesheetUtils.uniqueArray($scope.workLabelsList.concat(labels))
                            : labels;
                }
                $scope.jqlQuery = params.jqlQuery; // addworklog only
            });

            var submitHandler = getSubmitHandler($scope.dialogType);
            AP.events.on('dialog.submit', submitHandler);
            AP.dialog.disableCloseOnSubmit();
        }

    }
]);
