var appBaseModule = angular.module('appBase', [
    'ngRoute', 'localization',
    'talis.services.logging',
    'configuration', 'directives']);

appBaseModule.config(['$httpProvider', '$provide', function ($httpProvider, $provide) {
    if (!window.atlToken) {
        var location = '&' + window.location.search.substring(1);
        window.atlToken = getUrlParam(location, '&jwt');
    }
    if (window.atlToken) {
        $httpProvider.defaults.headers.common['Authorization'] = "JWT " + window.atlToken;
    }
    $provide.factory('authHttpInterceptor', ['$injector', '$q', function($injector, $q) {
        return {
            response: function(response) {
                var xAcpt = response.headers('X-acpt');
                if (xAcpt) {
                    window.atlToken = xAcpt;
                    // update authentication token
                    $httpProvider.defaults.headers.common['Authorization'] = "JWT " + window.atlToken;
                    //  schedule automatic refresh before token expires
                    $injector.get('authbeat').authbeat(); // lazy load
                }
                return response;
            },
            'responseError': function(rejection) {
                if (rejection.status == 401 && rejection.config.url != '/authbeat' && rejection.config.url != '/bitbucket/login') {
                    // TODO: apMessages or apService.messsages()
                    AP.flag.create({
                        title: 'Authentication expired',
                        body: 'Reload page to restore communication with add-on backend',
                        type: 'error'
                    });
                } else if (rejection.config.url =~ '/templates/') {
                    $injector.get('applicationLoggingService').trace('responseError', rejection); // lazy load
                }
                return $q.reject(rejection);
            }
        }
    }]);
    $provide.factory('urlRewriteHttpInterceptor', ['$injector', '$window', function($injector, $window) {
        return {
            request: function(config) {
                var path = config.url.match(/^\/?(.+?)\//);
                if (path && path[1] == 'i18n') {
                    config.url = $window.i18nDefault;
                }
                return config;
            }
        }
    }]);
    $httpProvider.interceptors.push('authHttpInterceptor', 'urlRewriteHttpInterceptor');
}]);

appBaseModule.run(['$rootScope', '$window' , '$http', '$injector', 'localize', 'applicationLoggingService',
        function($rootScope, $window, $http, $injector, localize, logger) {

    logger.flush(); // init

    var location = '&' + $window.location.search.substring(1);
    var v = getUrlParam(location, '&v');
    if (v == 3) {
        var license = getUrlParam(location, 'lic');
        if (license == 'active') {
            $window.storeWorklogEnabled = true;
            $rootScope.subscribe = true;
        }
        $injector.get('authbeat').authbeat(); // lazy load
    }

    // TODO: all context variables via html meta
    $rootScope.deployedDate = $window.deployedDate;
    localize.initLocalizedResources();
    $rootScope.initOnErrorPopup = function(params) {
        var configurationService = $injector.get('configurationService'); // lazy load for tests
        configurationService.getConfiguration().then(function(config) {
            logger.trace('mailer url, ignore?', [$window.mailerUrl, config.onErrorIgnore.val]);
            if ($window.mailerUrl && !config.onErrorIgnore.val) {
                $window.$onErrorPopup = params.dialog; // see templates/main.html and modules/logging.exceptionLoggingService
                params.scope.onErrorDialog = function() { // see templates/popups/onError.html
                    $onErrorPopup.hide();
                    var customData = {
                        errorMessage: $onErrorPopup.data('errorMessage'),
                        stackTrace: $onErrorPopup.data('stackTrace')
                    };
                    AP.dialog.create({
                        key: 'bitbucket',
                        width: '700px',
                        height: '260px',
                        chrome: true,
                        header: "Report Problem",
                        submitText: "Submit",
                        customData: customData
                    });
                };
                params.scope.onErrorIgnore = function() { // see onError.html
                    config.onErrorIgnore.val = true;
                    configurationService.saveConfiguration(config.onErrorIgnore).then(function () {
                        $window.$onErrorPopup.hide();
                        delete $window.$onErrorPopup;
                    });
                }
            }
        });
    };
}]);
appBaseModule.service('authbeat', ['$timeout', '$http', function ($timeout, $http) {
    this.authbeatPromise = null;
    this.authbeat = function() {
        $timeout.cancel(this.authbeatPromise);
        this.authbeatPromise = $timeout(function() {
            $http.get('/authbeat');
            // token remains valid for 15 minutes by default - configured using maxTokenAge.
        }, 14*60*1000, false);
    };
}]);

var getParam = function(param, $q, converter) {
    var deferred = $q.defer();
    window.AP.getLocation(function(location) {
        var result = getUrlParam(location, param);
        deferred.resolve(angular.isFunction(converter) ? converter(result) : result);
    });
    return deferred.promise;
};
var getUrlParam = function(location, param) {
    var result;
    var part = location.split(param + '=')[1];
    if (angular.isDefined(part)) {
        result = part.split(/&|#!/)[0] || "";
    }
    return result;
};

appBaseModule.factory('projectKey', ['$q', function ($q) {
    return getParam('project.key', $q);
}]);
appBaseModule.factory('externalFilter', ['$q', function ($q) {
    return getParam('filter', $q);
}]);
appBaseModule.factory('jql', ['$q', function ($q) {
    return getParam('jql', $q, function(value) {
        if (angular.isDefined(value)) {
            return decodeURIComponent(TimesheetUtils.replaceAll(value, '+', ' '));
        } else {
            return value;
        }
    });
}]);

appBaseModule.factory('timesheetParams', ['$q', '$routeParams', function ($q, $routeParams) {

    var deferred = $q.defer();
    window.AP.getLocation(function(location){
        var urlParts = location.split("#!");
        deferred.resolve(urlParts.length == 2
            ? TimesheetUtils.parseParams(urlParts[1])
            : $routeParams);
    });
    return deferred.promise;
}]);

appBaseModule.factory('loggedInUser', ['$q', 'pivottableService',
    function ($q, pivottableService) {

        var htmlEscapes = {
            '&': '&amp;',
            '<': '&lt;',
            '>': '&gt;',
            '"': '&quot;',
            "'": '&#39;'
        };

        var unEscape = function (str) {
            for (i in htmlEscapes) {
                str = str.replace(htmlEscapes[i], i);
            }
            return str;
        };

        var deferred = $q.defer();
        window.AP.getUser(function (user) {
            user.key = unEscape(user.key);
            pivottableService.getUserInfo(user.key).then(function (json) {
                moment.locale(json.locale); // set global locale
                // TODO? moment.tz.setDefault() when moment everywhere
                deferred.resolve(json);
            });
        });
        /* error
         $rootScope.loggedInUser = undefined;
         */
        return deferred.promise;
    }]);

appBaseModule.service('apService', ['$q', function ($q) {
    // TODO: messages, request
}]);
