app.filter('prettyHours', [function() {
    return function(input, config, isElapsedTime) {
        var value = (input && angular.isObject(input)) ? input.sum : parseInt(input) || 0;
        return value != 0 ? DrawHelper.getFormattedTime(value, config || {}, isElapsedTime) : "";
    }
}]);

app.filter('time', ['$sce', function($sce) {
    return function(input, config) {
        var value = (input && angular.isObject(input) && input.hasOwnProperty('value')) ? input.value : input;
        return value ? $sce.trustAsHtml( moment(value).format('LT')) : '';
    }
}]);

app.filter('timeago', [function() {
    return function(input) {
        var value = moment(input); // iso date string
        return value.isValid() ? value.fromNow() : input;
    }
}]);

app.filter('timeTracking', ['$sce', function($sce) {
    return function(column) {
        var value = column.value || column;
        if (value.timespent + value.estimate == 0) {
            return $sce.trustAsHtml("&nbsp;");
        }
        var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)); //calcColumnFunction(row);
        var leftPercents = 100 - percents;
        var html = '<table cellpadding="0" cellspacing="0" border="0"><tr>' +
            '<td style="padding: 0; margin: 0">' +
            '<table border="0" width="100px" style="font-size: 5px;" cellpadding="0" cellspacing="0"><tr>' +
            (percents > 0 ? '<td style="width:'+percents+'%; background-color: #51a825;">&nbsp;</td>' : '') +
            (leftPercents > 0 ? '<td style="width:'+leftPercents+'%; background-color: #ec8e00;">&nbsp;</td>' : '') +
            '</tr></table>' +
            '</td>' +
            '<td style="margin: 0; padding: 0 0 0 5px">' +
            percents + '%' +
            '</td>' +
            '</tr></table>';
        return $sce.trustAsHtml(html);
    }
}]);

app.filter("htmlSafe", ['$sce', function($sce) {
    return function(htmlCode){
        return $sce.trustAsHtml(htmlCode);
    };
}]);
