function ExcelView(allIssues) {
    this.allIssues = allIssues;
}

ExcelView.dateTimeFormat = 'YYYY-MM-DD HH:mm:ss';

// see also PivotKey.GroupByField
ExcelView.prototype.fieldValue = function(issue, fieldObject, options) {
    var NULL_VALUE = "";
    var sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[(.+)?\]/;
    var maxFractionDigits = options.maxFractionDigits
    var hostBaseUrl = options.hostBaseUrl || '';

    var getNameOrKey = function(field) {
        var m;
        if (angular.isObject(field) && field.hasOwnProperty('name')) {
            return field.name;
        } else if (angular.isObject(field) && field.hasOwnProperty('key')) {
            return "<a href='" + hostBaseUrl + "/browse/" + field.key + "'>" + field.key + "</a>";
        } else if (angular.isObject(field) && field.hasOwnProperty('value')) {
            return field.value + (field.hasOwnProperty('child') ? ':' + getNameOrKey(field.child) : '');
        } else if (angular.isString(field) && (m = field.match(sprintRegex))) {
            var splits = m[1].split(',');
            for (var i = 0; i < splits.length; i++) {
                var split = splits[i];
                if (split.indexOf('name=') == 0) {
                    return split.substr(5);
                }
            }
            return NULL_VALUE;
        } else {
            return field;
        }
    };

    var getValue = function(field) {
        if (TimesheetUtils.isDate(field)) {
            return moment(field).format(ExcelView.dateTimeFormat);
        } else if (Array.isArray(field)) {
            var value = '';
            if (field.length > 0 && (field[0].inwardIssue || field[0].outwardIssue)) {
                value = field.map(function(v) {
                    if (v.inwardIssue) {
                        return v.type.inward + ' ' + getNameOrKey(v.inwardIssue);
                    } else {
                      return v.type.outward + ' ' + getNameOrKey(v.outwardIssue);
                    }
                }).join(', ');
            } else {
                field.forEach(function(v) {
                    if (value.length > 0) {
                        value += ', ';
                    }
                    value += getNameOrKey(v);
                });
            }
            return value;
        } else {
            return getNameOrKey(field);
        }
    };

    var fieldName = fieldObject.id || fieldObject;

    var getFieldValueObject = function(issue) {
        var value = issue.fields[fieldName];
        if (fieldObject && fieldObject.schema && fieldObject.schema.custom == 'com.pyxis.greenhopper.jira:gh-epic-link') {
            if (value) {
                value = {key: value};
            } else if (issue.fields.issuetype.name == 'Epic') {
                value = {key: issue.key};
            }
        }
        if (fieldName == 'project') {
            value = NULL_VALUE;
        }
        if (!value) {
            if (fieldName == 'parent') {
                value = {key : issue.key};
            } else if (fieldName == 'projectcategory' && issue.fields.project.projectCategory) {
                value = issue.fields.project.projectCategory;
            } else {
                value = NULL_VALUE;
            }
        }
        return value;
    };

    this.field = getFieldValueObject(issue);
    var value = getValue(this.field);
    if (['timeestimate', 'timeoriginalestimate', 'timespent'].indexOf(fieldName) != -1 && value != NULL_VALUE) {
        value = DrawHelper.formatDecimal((value || 0) / 3600, maxFractionDigits) + 'h';
    }
    return value;
};
/*
    Provides raw worklogs.

    Supported options: pivotTableType, monthView, startDate, endDate, user
    TODO? groups, sumSubTask
*/
ExcelView.prototype.generate = function(options) {
    var hostBaseUrl = options.hostBaseUrl || '';
    var prepareLink = function(url, text) {
        return "<a href='" + hostBaseUrl + url + "'>" + text + "</a>";
    };
    var pivotTable = PivotTableFactory.createPivotTable(options);
    var excel = '<style>\n';
    excel += '<!--br\n';
    excel += '    {mso-data-placement:same-cell;}\n';
    excel += '<!--table\n';
    var separators = (1234.5).toLocaleString().match(/\d+(\D)?\d+(\D)\d+/);
    excel += '    {mso-displayed-decimal-separator:"\\' + separators[2] + '";';
    excel += (separators[1] ? '\n    mso-displayed-thousand-separator:"\\' + separators[1] + '";' : '') + '}\n';
    excel += '-->\n';
    excel += '</style>\n';
    excel += "<table>\n";
    excel += "  <tr>\n";
    excel += "    <td>Project</td>\n";
    excel += "    <td>Issue Type</td>\n";
    excel += "    <td>Key</td>\n";
    excel += "    <td>Summary</td>\n";
    excel += "    <td>Priority</td>\n";
    for (var i = 0; i < options.moreFields.length; i++) {
        var moreField = options.moreFields[i];
        if (['workeduser', 'workeddate'].indexOf(moreField.id) < 0) {
            excel += "    <td>" + moreField.name + "</td>\n";
        }
    }
    if (options.pivotTableType == "TimeTracking") {
        excel += "    <td>Original Estimate (h)</td>\n";
        excel += "    <td>Est. Time Remaining (h)</td>\n";
        excel += "    <td>Time Spent (h)</td>\n";
        excel += "    <td>Variance (h)</td>\n";
        excel += "    <td>Progress</td>\n";
    } else {
        excel += "    <td>Date Started</td>\n";
        if (options.pivotTableType == "IssuePassedTimeByStatus") {
            excel += "    <td>Date Ended</td>\n";
        }
        excel += "    <td>Username</td>\n";
        excel += "    <td>Display Name</td>\n";
        excel += "    <td>Time Spent (h)</td>\n";
        if (options.pivotTableType == "IssuePassedTimeByStatus") {
            excel += "    <td>Status</td>\n";
        } else {
            excel += "    <td>Work Description</td>\n";
        }
    }
    excel += "  </tr>\n";
    var totalTimeSpentSeconds = 0;
    for (var i in this.allIssues) {
        var pivotEntries = pivotTable.add(this.allIssues[i]);
        for (var i in pivotEntries) {
            excel += "  <tr>\n";
            var pivotEntry = pivotEntries[i];
            // rowKey is PivotKey.Issue
            var issue = pivotEntry.rowKey.issue;
            var worklog = pivotEntry.rowKey.worklog || pivotEntry.worklog; // for PassedTimeByStatus
            excel += "    <td>" + issue.fields.project.name + "</td>\n";
            excel += "    <td>" + issue.fields.issuetype.name + "</td>\n";
            excel += "    <td>" + prepareLink('/browse/' + issue.key, issue.key) + "</td>\n";
            excel += "    <td>" + prepareLink('/browse/' + issue.key, issue.fields.summary) + "</td>\n";
            excel += "    <td>" + this.fieldValue(issue, 'priority', options) + "</td>\n";
            for (var i = 0; i < options.moreFields.length; i++) {
                var moreField = options.moreFields[i];
                if (['workeduser', 'workeddate'].indexOf(moreField.id) < 0) {
                    if ('workcreated' == moreField.id) {
                        excel += "    <td>" + moment(worklog.created).format(ExcelView.dateTimeFormat) + "</td>\n";
                    } else {
                        excel += "    <td>" + this.fieldValue(issue, moreField, options) + "</td>\n";
                    }
                }
            }
            if (options.pivotTableType == "TimeTracking") {
                excel += "    <td>" + DrawHelper.formatDecimal(pivotEntry.values['1timeoriginalestimate'] / 3600, options.maxFractionDigits) + "</td>\n";
                excel += "    <td>" + DrawHelper.formatDecimal(pivotEntry.values['2esttimeremaining'] / 3600, options.maxFractionDigits) + "</td>\n";
                excel += "    <td>" + DrawHelper.formatDecimal(pivotEntry.values['3timespent'] / 3600, options.maxFractionDigits) + "</td>\n";
                excel += "    <td>" + DrawHelper.formatDecimal(pivotEntry.values['4diff'] / 3600, options.maxFractionDigits) + "</td>\n";
                // see timeTracking filter
                var value = pivotEntry.values['5progress'];
                if (value.timespent + value.estimate == 0) {
                  excel += "    <td></td>\n";
                } else {
                    var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)); //calcColumnFunction(row);
                    excel += "    <td>" + DrawHelper.formatDecimal(percents, options.maxFractionDigits) + "%</td>\n";
                }
            } else {
                excel += "    <td>" + moment(worklog.workDate).format(ExcelView.dateTimeFormat) + "</td>\n";
                if (options.pivotTableType == "IssuePassedTimeByStatus") {
                    excel += "    <td>" + moment(worklog.toDate).format(ExcelView.dateTimeFormat) + "</td>\n";
                }
                excel += "    <td>" + worklog.author.name + "</td>\n";
                excel += "    <td>" + (worklog.author.displayName || '') + "</td>\n";
                excel += "    <td>" + DrawHelper.formatDecimal(worklog.timeSpentSeconds / 3600, options.maxFractionDigits) + "</td>\n";
                excel += "    <td>" + (worklog._issue ? prepareLink('/browse/' + worklog._issue.key, worklog._issue.key) + ' ' : '') +
                    (options.pivotTableType == "IssuePassedTimeByStatus" ? pivotEntry.columnKey.keyValue : (worklog.comment || '')) + "</td>\n";
                excel += "  </tr>\n";
                totalTimeSpentSeconds += worklog.timeSpentSeconds;
            }
        }
    }
    excel += "  <tr>\n";
    excel += "    <td>Total</td>\n";
    var moreFieldsLength = 0;
    for (var i = 0; i < options.moreFields.length; i++) {
        var moreField = options.moreFields[i];
        if (['workeduser', 'workeddate'].indexOf(moreField.id) < 0) {
            moreFieldsLength++;
        }
    }
    var colspan = options.pivotTableType == "TimeTracking" ? 4 : 7;
    for (var i = 0; i < colspan + moreFieldsLength; i++) {
        excel += "    <td></td>\n";
    }
    if (options.pivotTableType == "IssuePassedTimeByStatus") {
        excel += "    <td></td>\n";
    }
    if (options.pivotTableType == "TimeTracking") {
        excel += "    <td>" + DrawHelper.formatDecimal(pivotTable.totals['1timeoriginalestimate'].sum / 3600, options.maxFractionDigits) + "</td>\n";
        excel += "    <td>" + DrawHelper.formatDecimal(pivotTable.totals['2esttimeremaining'].sum / 3600, options.maxFractionDigits) + "</td>\n";
        excel += "    <td>" + DrawHelper.formatDecimal(pivotTable.totals['3timespent'].sum / 3600, options.maxFractionDigits) + "</td>\n";
        excel += "    <td>" + DrawHelper.formatDecimal(pivotTable.totals['4diff'].sum / 3600, options.maxFractionDigits) + "</td>\n";
        // see timeTracking filter
        var value = pivotTable.totals['5progress'].value;
        if (value.timespent + value.estimate == 0) {
          excel += "    <td></td>\n";
        } else {
            var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)); //calcColumnFunction(row);
            excel += "    <td>" + DrawHelper.formatDecimal(percents, options.maxFractionDigits) + "%</td>\n";
        }
    } else {
        excel += "    <td>" + DrawHelper.formatDecimal(totalTimeSpentSeconds / 3600, options.maxFractionDigits) + "</td>\n"
        excel += "    <td></td>\n";
    }
    excel += "  </tr>\n";
    excel += "</table>\n";
    return excel;
};
ExcelView.prototype.download = function(options) {
    var excel = this.generate(options);
    var excelFile = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40">\n';
    excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel; charset=utf-8">';
    excelFile += "<head>\n";
    excelFile += "<!--[if gte mso 9]>\n";
    excelFile += "<xml>\n";
    excelFile += "<x:ExcelWorkbook>";
    excelFile += "<x:ExcelWorksheets>";
    excelFile += "<x:ExcelWorksheet>";
    excelFile += "<x:Name>";
    excelFile += "{worksheet}";
    excelFile += "</x:Name>";
    excelFile += "<x:WorksheetOptions>";
    excelFile += "<x:DisplayGridlines/>";
    excelFile += "</x:WorksheetOptions>";
    excelFile += "</x:ExcelWorksheet>";
    excelFile += "</x:ExcelWorksheets>";
    excelFile += "</x:ExcelWorkbook>\n";
    excelFile += "</xml>\n";
    excelFile += "<![endif]-->\n";
    excelFile += "</head>\n";
    excelFile += "<body>\n";
    excelFile += excel;
    excelFile += "</body>\n";
    excelFile += "</html>\n";

    var fileName = 'exportData.xls';
    var fileData = [excelFile];
    var blobObject = new Blob(fileData, {encoding:"utf-8", type: 'application/vnd.ms-excel;charset=utf-8;'});
    var isIE = /*@cc_on!@*/false || !!document.documentMode; // At least IE6
    if (isIE) {
        window.navigator.msSaveOrOpenBlob(blobObject, fileName);
    } else {
        var data;
        if (navigator.userAgent.indexOf('Safari') != -1 && navigator.userAgent.indexOf('Chrome') == -1) {
          var base64data = "base64," + AJS.$.base64.encode(excelFile);
          data = 'data:application/vnd.ms-excel;charset=utf-8;filename=' + fileName + ';' + base64data;
        } else {
          data = URL.createObjectURL(blobObject);
        }
        var link = document.createElement('a');
        if (typeof link.download === 'string') {
            link.href = data;
            link.download = fileName;
            //Firefox requires the link to be in the body
            document.body.appendChild(link);
            link.click();
            setTimeout(function() {
                document.body.removeChild(link);
            }, 100);
        } else {
            window.open(data);
        }
    }
};
